import { Dropdown, Container } from "semantic-ui-react";
import axios from "axios";
import Options from "./Options";
import Contents from "./Contents";
import img1 from "../Img/img4.svg";
import img3 from "../Img/img3.svg";
var React = require("react");

const userList = [];

const Questions = [
  "Peerly Questions",
  "Your Proudest moments in the last 6 months.",
  "What do you aspire to do more in the next 6 months.",
  "What do you want to do less of?",
  "Are you interested in company related responsibilities?",
  "What kind of projects you aspire to do?",
  "What training / learning you want help with?",
  "When there's an escalation in project, How do you suggest business team help you resolve it?",
  "Do you feel encouraged to question the company's rules, policies & values?",
  "What do you feel about the work-life balance at Codewave?"
];
class Self extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentStep: 1,
      questions: [
        { key: "question1", value: "" },
        { key: "question2", value: "" },
        { key: "question3", value: "" },
        { key: "question4", value: "" },
        { key: "question5", value: "" },
        { key: "question6", value: "" },
        { key: "question7", value: "" },
        { key: "question8", value: "" },
        { key: "question9", value: "" }
      ],
      proudMoments: "hjhj",
      requiredWarning: "Please give your thoughts on the same."
    };
  }

  // generic handle change event
  handleChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
    setTimeout(() => {
      this._next();
    }, 50);
  };

  //user assessment start first page
  userStartAssessment = event => {
    let userDropDownValue = document.getElementById("userDropDown").innerText;
    if (userDropDownValue !== "Select your name") {
      this.setState({
        userName: userDropDownValue
      });
      var employeeCheck = {
        employeeName: userDropDownValue,
        month: "February 2023"
      };
      this.setState({
        currentStep: 13
      });
      //axios post for checking user peer combo
      axios
        .post("http://13.126.234.226:5000/api/self/check", employeeCheck)
        .then(response => {
          if (response.status == 200) {
            this.setState({
              currentStep: 2
            });
          } else if (response.status == 302) {
            alert("you already gave your assessment");
          } else {
            alert("network issue #");
          }
        })
        .catch(error => {
          if (error.response == undefined) {
            alert("Please check your internet connection & try again");
            this.setState({
              currentStep: 1
            });
          } else if(error.response.status == 302){
            this.setState({
                  currentStep: 12
                });
          } else {
            alert("Something went wrong, Try again");
          }
        });
    } else {
      alert("please select one");
    }
  };

  //redirect to home from thankyou
  userGoHome = event => {
    window.location = window.location.origin + "/takeselfassessment";
  };

  //genericc handle content
  handleContent = event => {
    const { name, value } = event.target;
    const index = this.state.questions.map(e => e.key).indexOf(name);
    var tempArray = this.state.questions;
    tempArray[index].value = value;
    this.setState({
      questions: tempArray
    });
  };

  //previous bttn
  handlePreviousBttn = event => {
    let currentStep = this.state.currentStep;
    currentStep = currentStep <= 1 ? 1 : currentStep - 1;
    this.setState({
      currentStep: currentStep
    });
  };

  handleContentChange = event => {
    const { name, value } = event.target;
    var tempObj = this.state.questions.filter(object => object.key == name);
    if (tempObj[0].value.trim().length == 0) {
      this.setState({
        requiredWarning: "This field is required**"
      });
    } else {
      this.setState({
        requiredWarning: "Please give your thoughts on the same"
      });
      let currentStep = this.state.currentStep;

      currentStep += 1;
      this.setState({
        currentStep: currentStep
      });
    }
  };

  //handle company heklp
  completeAssessment = event => {
    const { name, value } = event.target;
    var tempObj = this.state.questions.filter(object => object.key == name);

    if (tempObj[0].value.trim().length == 0) {
      // console.log("field content missing");
      this.setState({
        requiredWarning: "This field is required**"
      });
    } else {
      this.setState({
        [name]: value
      });
      //for loader
      this.setState({
        currentStep: 13
      });
      //all questions object
      var allQuestions = {
        employeeName: this.state.userName,
        month: "February 2023",
        assessment: [
          {
            questionNo: "1",
            questionTitle: Questions[1],
            answer: this.state.questions[0].value
          },
          {
            questionNo: "2",
            questionTitle: Questions[2],
            answer: this.state.questions[1].value
          },
          {
            questionNo: "3",
            questionTitle: Questions[3],
            answer: this.state.questions[2].value
          },
          {
            questionNo: "4",
            questionTitle: Questions[4],
            answer: this.state.questions[3].value
          },
          {
            questionNo: "5",
            questionTitle: Questions[5],
            answer: this.state.questions[4].value
          },
          {
            questionNo: "6",
            questionTitle: Questions[6],
            answer: this.state.questions[5].value
          },
          {
            questionNo: "7",
            questionTitle: Questions[7],
            answer: this.state.questions[6].value
          },
          {
            questionNo: "8",
            questionTitle: Questions[8],
            answer: this.state.questions[7].value
          },
          {
            questionNo: "9",
            questionTitle: Questions[9],
            answer: this.state.questions[8].value
          }
        ]
      };

      //axios post
      axios
        .post("http://13.126.234.226:5000/api/self/", allQuestions)
        // .post("http://localhost:5000/api/self/", allQuestions)
        .then(response => {
          if (response.status == 200) {
            this.setState({
              currentStep: 11
            });
          } else if (response.status == 302) {
            alert("you already gave your assessment #");
          } else {
            alert("network issue #");
          }
        })
        .catch(error => {
          if (error.response == undefined) {
            this.setState({
              currentStep: 10
            });
            alert("Please check your internet connection & try again");
          } else if(error.response.status == 302){
            this.setState({
                  currentStep: 12
                });
          } else {
            alert("Something went wrong, Try again");
          }
        });
    }
  };

  //next step or page
  _next = () => {
    let currentStep = this.state.currentStep;
    currentStep += 1;
    this.setState({
      currentStep: currentStep
    });
  };

  //previous step or page
  _prev = () => {
    let currentStep = this.state.currentStep;
    currentStep = currentStep <= 1 ? 1 : currentStep - 1;
    this.setState({
      currentStep: currentStep
    });
  };

  //for showing hidding previous bttn
  previousButton() {
    let currentStep = this.state.currentStep;
    if (currentStep == 1) {
      return null;
    } else if (currentStep == 2) {
      return null;
    } else if (currentStep == 3) {
      return null;
    } else if (currentStep == 4) {
      return null;
    } else if (currentStep == 5) {
      return null;
    } else if (currentStep == 6) {
      return null;
    } else if (currentStep == 7) {
      return null;
    } else if (currentStep == 8) {
      return null;
    } else if (currentStep == 9) {
      return null;
    } else if (currentStep == 10) {
      return null;
    } else if (currentStep == 11) {
      return null;
    } else if (currentStep == 12) {
      return null;
    } else if (currentStep == 13) {
      return null;
    } else {
      return (
        <div className="genericBttnDiv">
          <button
            className="peerlySecondBttn"
            type="button"
            onClick={this._prev}
          >
            PREVIOUS
          </button>
        </div>
      );
    }
  }

  componentDidUpdate() {
    console.log("did update");
  }
  componentWillUpdate() {
    console.log("will update");
  }

  componentDidMount() {
    console.log("did mount");
    axios.get("http://13.126.234.226:5000/api/users/").then(response => {
      // axios.get("http://localhost:5000/api/users/").then(response =>{
      response.data.forEach(entry => {
        userList.push({
          key: entry.name,
          value: entry.name,
          text: entry.name
        });
      });
    });
  }
  render() {
    console.log("render");
    return (
      <React.Fragment>
        <form onSubmit={this.handleSubmit} class="formMobileFix">
          <Step1
            currentStep={this.state.currentStep}
            userStartAssessment={this.userStartAssessment}
            userName={this.userName}
          />
          <Step2
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[0].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />

          <Step3
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[1].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />
          <Step4
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[2].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />

          <Step5
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[3].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />

          <Step6
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[4].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />

          <Step7
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[5].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />

          <Step8
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[6].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />

          <Step9
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[7].value}
            handleContent={this.handleContent}
            handleContentChange={this.handleContentChange}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />

          <Step10
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            questionAnswer={this.state.questions[8].value}
            handleContent={this.handleContent}
            handleContentChange={this.completeAssessment}
            requiredWarning={this.state.requiredWarning}
            handlePreviousBtn={this.handlePreviousBttn}
          />
          <Step11
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            userGoHome={this.userGoHome}
          />

          <Step12
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            userGoHome={this.userGoHome}
          />
          <Step13
            userName={this.state.userName}
            currentStep={this.state.currentStep}
            userGoHome={this.userGoHome}
          />

          {this.previousButton()}
          {/* {this.nextButton()} */}
        </form>
      </React.Fragment>
    );
  }
}

function Step1(props) {
  if (props.currentStep !== 1) {
    return null;
  }
  return (
    <div className="testTransition1 peerlyHomeContainer">
      <div className="peerlyMainHead ">
        {" "}
        <h2 className="peerlyMainHeadText">CODEWAVE - PEERLY 2023</h2>
      </div>

      <p className="peerlySubHead">Self Assessment - February </p>

      <div className="peerlyImg peerlySelfImg">
        <img className="peerlyImg1" src={img1} alt="haii" />
      </div>
      <div className="peerlyDescriptionSection">
        <p className="peerlyDescriptionHead">
          <span className="peerlyDescriptionHeadSpan">
            Reflect on your last 6 months{" "}
          </span>{" "}
          and plan your future.
        </p>

        <div className="dropDownSection">
          <Dropdown
            id="userDropDown"
            placeholder="Select your name"
            fluid
            search
            selection
            value={props.userName}
            options={userList}
          />
        </div>

        <div className="customButtonBlock">
          <div className="startBttnSection">
            <button
              className="peerlyMainBttn float-right"
              type="button"
              onClick={props.userStartAssessment}
            >
              START
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

