import React, { Component } from "react";

class Contents extends Component {
  constructor() {
    super();
  }
  render() {
    return (
      <div className="testTransition1 peerlyHomeContainer">
        <div className="peerlyMainHead ">
          {" "}
          <h2 className="peerlyMainHeadText">
            {" "}
            QUESTION {this.props.currentStep - 1}/9
          </h2>
        </div>

        <p className="peerlySubHead">
          Self assessment of {this.props.userName} - February 2023
        </p>
        <p className="peerlyQuestion peerlyContentQHead">
          {this.props.question}
        </p>
        <div class="paper">
          <div class="paper-content">
            <textarea
              autoFocus
              name={this.props.questionTag}
              value={this.props.questionAnswer}
              onChange={this.props.handleContent}
              placeholder={this.props.requiredWarning}
            ></textarea>
          </div>
        </div>
        <div className="contentBttns">
          {this.props.currentStep == 2 ? (
            " "
          ) : (
              <button
                className="peerlySecondBttn"
                type="button"
                onClick={this.props.handlePreviousBtn}
              >
                PREVIOUS
              </button>
            )}

          <button
            className="peerlyMainBttn peerlyCBttn2"
            name={this.props.questionTag}
            type="button"
            onClick={this.props.handleContentChange}
          >
            {this.props.nextButton}
          </button>
        </div>
      </div>
    );
  }
}

export default Contents;
