import { Dropdown, Input, Container } from "semantic-ui-react";
import axios from "axios";
import img5 from "../Img/img5.svg";
import img6 from "../Img/img6.svg";
var React = require("react");


const userList = [];

class Report extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentStep: 1
        };
    }


    //report start first page
    handleAdminLogin = event => {
        let loginPassword = document.getElementById("loginPassword").value;
        if (loginPassword) {

            var loginPass = {
                pass: loginPassword
            };

            //axios post for checking login
            axios
                .post("http://13.126.234.226:5000/api/report/checkAdmin", loginPass)
                .then(response => {
                    if (response.status == 200) {
                        let currentStep = this.state.currentStep;
                        currentStep += 1;
                        this.setState({
                            currentStep: currentStep
                        });
                    } else if (response.status == 302) {
                        alert("Admin not found");
                    } else {
                        alert("network issue #");
                    }
                })
                .catch(error => {
                    if (error.response.status == 302) {
                        alert("Password don't match");
                    } else if (error.response.status == 400) {
                        alert("Something went wrong");
                    }
                });
        } else {
            alert("Please type in the password");
        }
    };




    //user assessment start first page
    handleDownloadReport = event => {
        let userDropDownValue = document.getElementById("userDropDown").innerText;
        // alert(userDropDownValue);
        if (userDropDownValue !== "Select the employee") {
            this.setState({
                employeeName: userDropDownValue
            });

            var employeeReport = {
                employeeName: userDropDownValue
            };
            var link = document.createElement("a");
            var linka = 'http://13.126.234.226:5000/api/report/peerlyreports';
            // var linka = 'http://localhost:5000/api/report/peerlyreports';
            var linkb = "/" + userDropDownValue;
            link.id = 'someLink';
            link.href = linka + linkb;
            link.click();

        } else {
            alert("Please select the Peer");
        }
    };

    //redirect to home from thankyou
    userGoHome = event => {
        window.location = window.location.origin + "/generatereport";
    };
    //previous bttn
    handlePreviousBttn = event => {
        let currentStep = this.state.currentStep;
        currentStep = currentStep <= 1 ? 1 : currentStep - 1;
        this.setState({
            currentStep: currentStep
        });
    };
    //next step or page
    _next = () => {
        let currentStep = this.state.currentStep;
        currentStep += 1;
        this.setState({
            currentStep: currentStep
        });
    };

    //previous step or page
    _prev = () => {
        let currentStep = this.state.currentStep;
        currentStep = currentStep <= 1 ? 1 : currentStep - 1;
        this.setState({
            currentStep: currentStep
        });
    };

    //for showing hidding previous bttn
    previousButton() {
        let currentStep = this.state.currentStep;
        if (currentStep == 1) {
            return null;
        } else if (currentStep == 2) {
            return null;
        } else if (currentStep == 3) {
            return null;
        } else if (currentStep == 4) {
            return null;
        } else if (currentStep == 5) {
            return null;
        } else if (currentStep == 6) {
            return null;
        } else if (currentStep == 7) {
            return null;
        } else if (currentStep == 8) {
            return null;
        } else if (currentStep == 9) {
            return null;
        } else if (currentStep == 10) {
            return null;
        } else if (currentStep == 11) {
            return null;
        } else if (currentStep == 12) {
            return null;
        } else {
            return (
                <div className="genericBttnDiv">
                    <button
                        className="peerlySecondBttn"
                        type="button"
                        onClick={this._prev}
                    >
                        PREVIOUS
          </button>
                </div>
            );
        }
    }

    componentDidUpdate() {
        console.log("did update");
    }
    componentWillUpdate() {
        console.log("will update");
    }

    componentDidMount() {
        axios.get("http://13.126.234.226:5000/api/users/").then(response => {
            // console.log(response, "respomnsefefefe");

            response.data.forEach(entry => {
                userList.push({
                    key: entry.name,
                    value: entry.name,
                    text: entry.name
                });
            });
        });
    }
    render() {
        return (
            <React.Fragment>
                <form onSubmit={this.handleSubmit} class="formMobileFix">
                    <Step1
                        currentStep={this.state.currentStep}
                        handleAdminLogin={this.handleAdminLogin}
                    // userName={this.userName}
                    />
                    <Step2
                        // userName={this.state.userName}
                        currentStep={this.state.currentStep}
                        // questionAnswer={this.state.questions[0].value}
                        // handleContent={this.handleContent}
                        handleDownloadReport={this.handleDownloadReport}
                    // requiredWarning={this.state.requiredWarning}
                    // handlePreviousBtn={this.handlePreviousBttn}
                    />

                    <Step3
                        // userName={this.state.userName}
                        currentStep={this.state.currentStep}
                        // questionAnswer={this.state.questions[1].value}
                        // handleContent={this.handleContent}
                        // handleContentChange={this.handleContentChange}
                        // requiredWarning={this.state.requiredWarning}
                        handlePreviousBtn={this.handlePreviousBttn}
                    />
                    <Step4
                    // userName={this.state.userName}
                    // currentStep={this.state.currentStep}
                    // questionAnswer={this.state.questions[2].value}
                    // handleContent={this.handleContent}
                    // handleContentChange={this.handleContentChange}
                    // requiredWarning={this.state.requiredWarning}
                    // handlePreviousBtn={this.handlePreviousBttn}
                    />


                    {this.previousButton()}
                    {/* {this.nextButton()} */}
                </form>
            </React.Fragment>
        );
    }
}

function Step1(props) {
    if (props.currentStep !== 1) {
        return null;
    }
    return (
        <div className="testTransition1 peerlyHomeContainer">
            <div className="peerlyMainHead ">
                {" "}
                <h2 className="peerlyMainHeadText">CODEWAVE - PEERLY 2023</h2>
            </div>

            <p className="peerlySubHead">Report - February </p>

            <div className="peerlyImg peerlySelfImg">
                <img className="peerlyImg1" src={img5} alt="haii" />
            </div>
            <div className="peerlyDescriptionSection">
                <p className="peerlyDescriptionHead">
                    <span className="peerlyDescriptionHeadSpan">
                        Type in the password to login{" "}
                    </span>{" "}
                    and download the reports.
        </p>

                <div className="dropDownSection">
                    <Input placeholder='Password' id="loginPassword" />
                </div>

                <div className="customButtonBlock">
                    <div className="startBttnSection">
                        <button
                            className="peerlyMainBttn float-right"
                            type="button"
                            onClick={props.handleAdminLogin}
                        >
                            LOGIN
            </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

function Step2(props) {
    if (props.currentStep !== 2) {
        return null;
    }
    return (
        <div className="testTransition1 peerlyHomeContainer">
            <div className="peerlyMainHead ">
                {" "}
                <h2 className="peerlyMainHeadText">CODEWAVE - PEERLY 2023</h2>
            </div>

            <p className="peerlySubHead">Report - February </p>

            <div className="peerlyImg peerlySelfImg">
                <img className="peerlyImg1" src={img6} alt="haii" />
            </div>
            <div className="peerlyDescriptionSection">
                <p className="peerlyDescriptionHead">
                    <span className="peerlyDescriptionHeadSpan">
                        Select the peer{" "}
                    </span>{" "}
                    to download the report.
        </p>

                <div className="dropDownSection">
                    <Dropdown
                        id="userDropDown"
                        placeholder="Select the employee"
                        fluid
                        search
                        selection
                        value={props.userName}
                        options={userList}
                    />
                </div>

                <div className="customButtonBlock">
                    <div className="startBttnSection">
                        <button
                            className="peerlyMainBttn float-right"
                            type="button"
                            onClick={props.handleDownloadReport}
                        >
                            DOWNLOAD
            </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

function Step3(props) {
    if (props.currentStep !== 3) {
        return null;
    }
    return (
        <div className="testTransition1 peerlyHomeContainer">
            <div className="peerlyMainHead ">
                {" "}
                <h2 className="peerlyMainHeadText"> SELF ASSESSMENT PENDING </h2>
            </div>

            <p className="peerlySubHead">
                Report - February 2023
      </p>
            <p className="question contentQuestion">
                Self assessment pending for this employee !!
      </p>
        </div>
    );
}

function Step4(props) {
    if (props.currentStep !== 4) {
        return null;
    }
    return (
        <div className="testTransition1 peerlyHomeContainer">
            <div className="peerlyMainHead ">
                {" "}
                <h2 className="peerlyMainHeadText"> REVIEW PENDING </h2>
            </div>

            <p className="peerlySubHead">
                Report - February 2023
        </p>
            <p className="question contentQuestion">
                Review pending for this employee !!
        </p>
        </div>
    );
}


export default Report;
